import account from '../../api/account';

// initial state
const state = {
  user: {
    userName: '',
    displayName: '',
    password: '',
  },
};

// getters
const getters = {
  getAccount: state => state.user,
};

// actions
const actions = {
  updateUserName({ commit }, credentials) {
    account.login(credentials, (response) => {
      commit('updateUserName', response);
    });
  },
};

// mutations
const mutations = {
  updateUserName(state, response) {
    state.user.userName = response.username;
    state.user.displayName = response.display_name;
    console.log(response);
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};



