<template>
  <v-app>
    <v-toolbar
      app
    >
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-layout pt-3 v-if="!account.user.userName">
        <v-flex xs3 offset-xs5>
          <v-text-field prepend-icon="person" name="login" label="Login" type="text"
                        v-model="userName"></v-text-field>
        </v-flex>
        <v-flex xs3>
          <v-text-field prepend-icon="lock" name="password" label="Password" id="password"
                        v-model="password"
                        type="password"></v-text-field>
        </v-flex>
        <v-flex xs1>
          <v-btn color="primary" :disabled="(!userName || !password)" @click="login()">Login</v-btn>
        </v-flex>
      </v-layout>
      <v-layout pt-3 v-if="account.user.userName">
        <v-flex xs3 offset-xs9>
          {{account.user.userName}}
        </v-flex>
      </v-layout>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer fixed app>
      <span class="copyleft">&copy;</span>
      <span>2018</span>
    </v-footer>
  </v-app>
</template>

<script>
  import { mapGetters } from 'vuex';

  export default {
    name: 'App',
    data() {
      return {
        title: 'PR checker',
        userName: '',
        password: '',
      };
    },
    methods: {
      login() {
        this.$store.dispatch('updateUserName', { userName: this.userName, password: this.password });
      },
    },
    computed: {
      account() {
        console.log(this.$store.state.account);
        return this.$store.state.account;
        // { user: { userName: '', displayName: '' } };
      },
      ...mapGetters(['getAccount']),
    },
  };
</script>
<style scoped>
  .copyleft {
    display: inline-block;
    transform: rotate(180deg);
  }
</style>



